.bodegas-page{background-color:#fff}.bodegas-hero{position:relative;min-height:500px;background:linear-gradient(135deg, #F16700 0%, #841C03 100%);display:flex;align-items:center;justify-content:center;padding:3rem 0;overflow:hidden}.bodegas-hero__overlay{position:absolute;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,.2);z-index:1}.bodegas-hero__container{position:relative;z-index:2;text-align:center;color:#fff}.bodegas-hero__title{font-family:"Outfit",sans-serif;font-size:clamp(28px,5vw,48px);font-weight:700;color:#fff;margin-bottom:1rem;text-shadow:2px 2px 4px rgba(0,0,0,.3)}.bodegas-hero__subtitle{font-family:"Hind",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:400;color:#fff;margin-bottom:2rem;text-shadow:1px 1px 3px rgba(0,0,0,.3)}.bodegas-hero__cta{font-family:"Outfit",sans-serif;font-weight:600;padding:.5rem 2rem;font-size:clamp(14px,1.5vw,18px)}@media(max-width: 768px){.bodegas-hero{min-height:400px;padding:2rem 0}}@media(max-width: 576px){.bodegas-hero{min-height:350px;padding:1.5rem 0}}.bodegas-section{padding:3rem 0;background-color:#fff}.bodegas-section--gray{background-color:#f5f5f5}.bodegas-section__title{font-family:"Outfit",sans-serif;font-size:clamp(24px,4vw,40px);font-weight:700;color:#000;text-align:center;margin-bottom:2rem}.bodegas-section__description{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;text-align:center;max-width:800px;margin:0 auto 1.5rem auto}@media(max-width: 768px){.bodegas-section{padding:2rem 0}}@media(max-width: 576px){.bodegas-section{padding:1.5rem 0}}.bodegas-card{background-color:#fff;border-radius:8px;padding:1.5rem;height:100%;box-shadow:0 2px 8px rgba(0,0,0,.1);transition:transform .3s ease,box-shadow .3s ease}.bodegas-card:hover{transform:translateY(-4px);box-shadow:0 4px 16px rgba(0,0,0,.15)}.bodegas-card--featured{border:2px solid #f16700}.bodegas-card__title{font-family:"Outfit",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:600;color:#000;margin-bottom:.5rem}.bodegas-card__text{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;line-height:1.6;margin-bottom:.5rem}.bodegas-card__specs{background-color:#f5f5f5;border-radius:4px;padding:.5rem;margin-bottom:1rem}.bodegas-card__specs p{font-family:"Hind",sans-serif;font-size:clamp(12px,1.2vw,14px);color:#333;margin-bottom:.25rem}.bodegas-card__specs p:last-child{margin-bottom:0}.bodegas-card__specs p strong{color:#000}.bodegas-card__price{font-family:"Outfit",sans-serif;font-size:clamp(20px,3vw,32px);font-weight:700;color:#f16700;margin-bottom:.25rem}.bodegas-card__disclaimer{font-family:"Hind",sans-serif;font-size:clamp(12px,1.2vw,14px);color:#ccc;font-style:italic;display:block}.bodegas-custom{background-color:#fff;border-radius:8px;padding:1rem;height:100%;border-left:4px solid #f16700}.bodegas-custom__title{font-family:"Outfit",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:600;color:#f16700;margin-bottom:.5rem}.bodegas-custom__list{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;list-style:none;padding-left:0;margin:0}.bodegas-custom__list li{padding-left:1rem;position:relative;margin-bottom:.25rem}.bodegas-custom__list li:before{content:"•";position:absolute;left:0;color:#f16700;font-weight:700;font-size:1.2em}.bodegas-custom__list li:last-child{margin-bottom:0}.bodegas-application{background-color:rgba(241,103,0,.05);border-radius:8px;padding:1.5rem;height:100%;border-top:3px solid #f16700}.bodegas-application__title{font-family:"Outfit",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:700;color:#000;margin-bottom:.5rem}.bodegas-application__text{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;line-height:1.6;margin-bottom:.5rem}.bodegas-application__text:last-child{margin-bottom:0}.bodegas-security{background-color:#fff;border-radius:8px;padding:1.5rem;height:100%;box-shadow:0 2px 8px rgba(0,0,0,.1)}.bodegas-security__title{font-family:"Outfit",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:600;color:#841c03;margin-bottom:.5rem;padding-bottom:.25rem;border-bottom:2px solid #841c03}.bodegas-security__text{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;line-height:1.6}.bodegas-gallery-item{border-radius:8px;overflow:hidden;box-shadow:0 2px 8px rgba(0,0,0,.1);transition:transform .3s ease}.bodegas-gallery-item:hover{transform:scale(1.05)}.bodegas-gallery-item__placeholder{background:linear-gradient(135deg, #F5F5F5, #CCCCCC);height:250px;display:flex;align-items:center;justify-content:center;text-align:center;font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333}.bodegas-gallery-item__placeholder p{margin:0;padding:1rem}@media(max-width: 576px){.bodegas-gallery-item__placeholder{height:200px}}.bodegas-cta{background:linear-gradient(135deg, #F16700 0%, #841C03 100%);padding:3rem 0;color:#fff}.bodegas-cta__title{font-family:"Outfit",sans-serif;font-size:clamp(24px,4vw,40px);font-weight:700;color:#fff;margin-bottom:1rem}.bodegas-cta__text{font-family:"Hind",sans-serif;font-size:clamp(18px,2.5vw,24px);color:#fff;margin-bottom:2rem;max-width:700px;margin-left:auto;margin-right:auto}.bodegas-cta__buttons{display:flex;justify-content:center;flex-wrap:wrap;gap:1rem}@media(max-width: 768px){.bodegas-cta{padding:2rem 0}}@media(max-width: 576px){.bodegas-cta__buttons{flex-direction:column;align-items:center}.bodegas-cta__buttons .btn{width:100%;max-width:300px}}